@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,300;1,400&display=swap');

html, body {
  margin: 0;
  height: 100%;
  overflow: hidden
}

body {
  background-image: url("../assets/background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 45%;    
}
  
p, a, h1, h2, h3 {
  font-family: 'Montserrat', sans-serif;
}

.main-nav ul {
    display: inline-flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 100vw;
    padding: 50px; 
    margin: 0;
    list-style: none;
  }
  .main-nav #logo {
    margin-right: 50vw;
  }
  
  .main-nav ul li{
    font-weight: 400;
    font-size: 1.5vw;
    margin-left: 50px;
  }
  
  .main-nav ul li a {
    text-decoration: none;
    color: #ffffff;
    text-align: center;
  }
  
  .main-nav ul li a:hover {
    text-decoration: underline;
  }
  
  /* Add an active class to highlight the current page */
  .main-nav ul li a:active{
    color: #e5a371;
  }
  
  .icon:hover{
    color: #ef8a3c;
  }
  /* Hide the link that should open and close the topnav on small screens */
  .main-nav .icon {
    display: none;
  }
  
  /* The links hide when the screen is less than 600 pixels wide. (.icon) will be shown to open and close the nav. */
  @media screen and (max-width: 600px) {
    .main-nav ul li a {display: none;}
    .main-nav a.icon {
      float: right;
      display: block;
    }
  }
  
  /* I add the "responsive" class to the main-nav with JavaScript so that  when the user clicks on the icon, the main-nav corresponds to the size of smaller screens. It displays the links vertically instead of horizontally */
  @media screen and (max-width: 600px) {
    .main-nav.responsive {
      position: relative;
    }
    .main-nav.responsive a.icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    .main-nav.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }

/* ====== Styles for the add new project button ====== */
.body-container {
    margin: 30px 10%;
}

.body-container h1 {
    margin: 10px 30px;
    display: block;
    color: #38382c;
    font-size: 30px;
}

.projectList {
    display: flex;
    flex-flow: row wrap;
}

.projectColWrapper {
    margin: 30px;
}

#addProject {
    transform: translateY(23%);
    background: transparent;
    font-size: 80px;
    border: none;
    color: #6E703D;
}

.projectCol {
    text-align: center;
    width: 150px;
    height: 210px;
    cursor: pointer;
    border: 1.5px solid #27271E;
    border-radius: 8px;
    padding-top: 20px;
}

.templateHTML .projectCol {
    // background-image: url('../../img/prevTemp.png');
    background-size: contain;
}

.projectCol:hover {
    box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.281);
    transform: translateY(-1px);
}

.projectCol p {
    display: block;
    text-align: left;
    font-size: 11px;
    padding: 0px 6px;
}

.projectCol i {
    text-align: right;
}

.nameBar {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}

.nameBar i:hover {
    box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.281);
    transform: translateY(-1px);
}

.info {
    display: block;
    transform: translateY(145px);
    padding: 15px 0px;
    background-color: #FFF;
    border-top: 1px solid #27271E;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.info p {
    background-color: #FFF;
}

/* ====== Create new project pop up window asking for basic information input ====== */
.newProjectPopUp {
    display: none;
    border-style: solid;
    border-radius: 8px;
    border-color: #27271E;
    border-width: 2px;
    background-color: #FFF;
    width: 30%;
    height: 70%;
    padding: 30px 40px;
    position: absolute;
    font-family: poppins;
    transform: translate(-50%, -20%);
    left: 50%;
    top: 30%;
}

.newProjectPopUp #done {
    font-size: 20px;
    padding: 7px 30px;
    position: absolute;
    color: #FFFCF9;
    background-color: #6E703D;
    border: 2px solid #6E703D;
    border-radius: 100px;
}

.newProjectPopUp button {
    margin-top: 40px;
}

.newProjectPopUp h1 {
    margin: 0px;
    background-color: transparent;
}

/* Create new project by entering properties form */
#newProjectForm {
    background-color: transparent;
}

#newProjectForm label {
    background-color: transparent;
    display: block;
}

#newProjectForm ul {
    background-color: transparent;
    font-size: 20px;
    padding: 0;
    margin-top: 10px;
}

#newProjectForm ul li {
    list-style: none;
    background-color: transparent;
}

#newProjectForm li+li {
    margin-top: 20px;
}

#newProjectForm input {
    width: 85%;
    background-color: transparent;
    margin-top: 10px;
    padding: 10px 20px;
    border-radius: 8px;
    border: 2px solid #27271E;
}

#newProjectForm select {
    width: 50%;
    height: auto;
    background-color: transparent;
    margin-top: 10px;
    padding: 10px 20px;
    border-radius: 8px;
    border: 2px solid #27271E;
}

.fa-times {
    display: block;
    margin: 0 0 20px auto;
    background-color: transparent;
    font-size: 25px;
    color: #6E703D;
    text-align: right;
}

@media (max-width: 900px) {
    .newProjectPopUp {
        width: 50%;
        height: 70%;
    }
}